h6,
.heading-style-h6, h5,
.heading-style-h5, h4,
.heading-style-h4, h3,
.heading-style-h3, h2,
.heading-style-h2, h1,
.heading-style-h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "FT Sterling", Verdana, sans-serif;
  font-weight: 600;
}

.text-size-tiny, .text-size-small, .text-size-regular, .text-size-medium, .text-size-large, .text-size-xlarge, body {
  margin: 0;
  font-family: "Mona Sans", Verdana, sans-serif;
  font-weight: 400;
}

.section__cta button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  border-radius: calc(infinity * 1px);
  font-family: "SF Pro";
}

body * {
  color: inherit;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

section,
.section {
  position: relative;
  overflow: hidden;
}

a {
  color: inherit;
  text-decoration: inherit;
  font-size: inherit;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.container-small,
.container-medium,
.container-large {
  margin-right: auto !important;
  margin-left: auto !important;
}

body {
  font-size: 1.125rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}

h1,
.heading-style-h1 {
  font-size: 4.75rem;
  line-height: 1.2em;
  letter-spacing: 0.01em;
}
@media only screen and (max-width: 767px) {
  h1,
  .heading-style-h1 {
    font-size: 3.75rem;
  }
}

h2,
.heading-style-h2 {
  font-size: 4rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}
@media only screen and (max-width: 767px) {
  h2,
  .heading-style-h2 {
    font-size: 3.25rem;
  }
}

h3,
.heading-style-h3 {
  font-size: 3rem;
  line-height: 1.4em;
  letter-spacing: 0.04em;
}
@media only screen and (max-width: 767px) {
  h3,
  .heading-style-h3 {
    font-size: 2.5rem;
  }
}

h4,
.heading-style-h4 {
  font-size: 2rem;
  line-height: 1.4em;
  letter-spacing: 0.04em;
}
@media only screen and (max-width: 767px) {
  h4,
  .heading-style-h4 {
    font-size: 1.5rem;
  }
}

h5,
.heading-style-h5 {
  font-size: 1.75rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}
@media only screen and (max-width: 767px) {
  h5,
  .heading-style-h5 {
    font-size: 1.375rem;
  }
}

h6,
.heading-style-h6 {
  font-size: 1.5rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}
@media only screen and (max-width: 767px) {
  h6,
  .heading-style-h6 {
    font-size: 1.25rem;
  }
}

.text-size-xlarge {
  font-size: 1.5rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}

.text-size-large {
  font-size: 1.25rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}

.text-size-medium {
  font-size: 1.125rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}

.text-size-regular {
  font-size: 1rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}

.text-size-small {
  font-size: 1rem;
  line-height: 1.4em;
  letter-spacing: -0.03em;
}

.text-size-tiny {
  font-size: 0.875rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}

.section {
  width: 100%;
  background-color: #D9DED2;
  border-bottom: 1px solid #ADB4A4;
}
.section__nav {
  padding: 28px 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 102px;
}
.section__menu {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "SF Pro";
  list-style: none;
  font-size: 14px;
  gap: 36px;
  text-transform: uppercase;
  color: #465039;
}
@media (max-width: 991px) {
  .section__menu {
    display: none;
  }
}
.section__cta {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "SF Pro";
  font-size: 14px;
  color: #465039;
  text-transform: uppercase;
  gap: 36px;
}
@media (max-width: 991px) {
  .section__cta p {
    display: none;
  }
}
.section__cta button {
  color: #465039;
  width: 125px;
  height: 46px;
  background-color: transparent;
  border: 1px solid #5E6653;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  background-color: #E2E7DB;
}
.footer__info {
  padding: 76px 80px;
  display: flex;
  align-items: start;
  justify-content: start;
  width: 100%;
  gap: 86px;
}
@media (max-width: 991px) {
  .footer__info {
    flex-direction: column;
    padding: 76px 40px;
    gap: 64px;
  }
}
.footer__info--item {
  display: flex;
  justify-content: space-between;
  align-items: start;
  padding-left: 86px;
  width: 100%;
  border-left: 1px solid #ADB4A4;
}
@media (max-width: 991px) {
  .footer__info--item {
    border-left: none;
    padding-top: 64px;
    padding-left: 0;
    border-top: 1px solid #ADB4A4;
  }
}
.footer__info--item--list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 20px;
}
.footer__info--item--list--title {
  font-size: 32px;
  font-family: "Ivy Mode";
  font-weight: 300;
  color: #465039;
  text-transform: uppercase;
}
.footer__info--item--list--menu {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: start;
  list-style: none;
  padding: 0;
  gap: 12px;
  color: #465039;
  text-transform: capitalize;
  font-family: "SF Pro";
  font-size: 18px;
}
.footer__info--item:first-child {
  padding: 0;
  gap: 36px;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  width: fit-content;
  border: none;
}
@media (max-width: 991px) {
  .footer__info--item:first-child img {
    width: 60%;
  }
}
.footer__info--item:first-child p {
  width: 399px;
  font-size: 18px;
  color: #5E6653;
  font-family: "SF Pro";
}
.footer__text {
  text-align: center;
  width: 100%;
  padding: 24px 0;
  font-size: 18px;
  font-family: "SF Pro";
  font-weight: 300;
  color: #465039;
  background-color: #E2E7DB;
  border-top: 1px solid #ADB4A4;
}